* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body,
span,
button,
textarea,
input {
  font-family: "Open Sans", "Arial", sans-serif;
}
body {
  margin: 0 auto;
}
.container {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 100%;
    max-width: 100%;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
    margin: 0 auto;
  }
}
@media (min-width: 1150px) {
  .container {
    padding: 0 50px;
    margin: 0 auto;
    max-width: 1150px;
  }
}
.main-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 79px;
}
.main-header__logo {
  padding: 22px 117px;
}
.main-nav__wrapper {
  position: absolute;
  left: 0;
  top: 79px;
  width: 100%;
  z-index: 1;
  background-color: #ffffff;
}
.main-nav__toggle {
  position: absolute;
  right: 32px;
  top: 32px;
  width: 20px;
  height: 14px;
  border: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
}
.main-nav__toggle span {
  display: block;
  background-color: #000000;
  width: 20px;
  height: 2px;
}
.main-nav__toggle span:not(:last-child) {
  margin-bottom: 4px;
}
.main-nav__bottom-delivery {
  display: none;
}
.main-header__logo-tablet,
.main-header__logo-desktop {
  display: none;
}
.main-header__logo-link svg {
  transition-timing-function: ease-out;
  transition-duration: 0.1s;
}
.main-header__logo-link:hover svg {
  opacity: 0.6;
}
.main-header__logo-link:active svg {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-header {
    height: 88px;
  }
  .main-nav__wrapper {
    display: flex;
    top: 0;
    flex-direction: column-reverse;
  }
  .main-nav__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid #ececec;
    background-color: #f9f9f9;
  }
  .main-nav__bottom-secondary {
    background-color: #f1f1f1;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-header__logo-tablet {
    display: block;
    position: absolute;
    z-index: 2;
    left: 50px;
  }
  .main-header__logo-mobile {
    display: none;
  }
}
@media (min-width: 1150px) {
  .main-header {
    position: relative;
    margin: 0 -50px;
  }
  .main-header__logo-tablet {
    display: none;
  }
  .main-header__logo-desktop {
    display: block;
    position: absolute;
    top: 8px;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0 auto;
    z-index: 2;
  }
  .main-nav__bottom {
    position: relative;
  }
}
.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  padding: 0 30px 15px;
  max-width: 320px;
  margin: 0 auto;
}
.footer::before {
  content: "";
  position: absolute;
  left: 0;
  background-color: #f1f1f1;
  width: 100%;
  height: 160px;
  z-index: -1;
}
.footer__logo {
  display: none;
  cursor: pointer;
}
.footer__list {
  display: grid;
  grid-template-columns: 28px 28px 28px;
  gap: 29px;
  list-style: none;
  padding: 25px 0 28px;
  margin: 0;
}
.footer__item {
  flex-basis: 33%;
}
.footer__link {
  color: #444444;
}
.footer__copyright {
  margin-top: 17px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}
.footer__copyright b {
  font-weight: 700;
  font-size: 19px;
  line-height: 18px;
  margin-right: 27px;
}
.footer__copyright::before {
  content: "";
  position: absolute;
  top: -22px;
  left: -39px;
  background-color: #e5e5e5;
  width: 260px;
  height: 1px;
}
.footer__logo svg {
  transition-timing-function: ease-out;
  transition-duration: 0.1s;
}
.footer__logo svg:hover {
  fill: #63D1BB;
}
.footer__logo svg:active {
  opacity: 0.3;
}
.footer__link svg {
  transition-timing-function: ease-out;
  transition-duration: 0.1s;
}
.footer__link svg:hover {
  fill: #63D1BB;
}
.footer__link svg:active {
  opacity: 0.3;
}
.footer__copyright svg {
  transition-timing-function: ease-out;
  transition-duration: 0.1s;
}
.footer__copyright:hover svg {
  opacity: 0.6;
}
.footer__copyright:active svg {
  opacity: 0.3;
}
.footer__copyright a:focus-visible {
  opacity: 0.6;
  outline: none;
}
@media (min-width: 768px) {
  .footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    margin: 0 auto;
    max-width: 768px;
    padding: 43px 0 46px;
  }
  .footer::before {
    height: 143px;
  }
  .footer__logo {
    display: block;
  }
  .footer__list {
    padding: 0;
  }
  .footer__copyright::before {
    display: none;
  }
  .footer__copyright {
    margin-top: -5px;
  }
}
@media (min-width: 1150px) {
  .footer {
    padding: 42px 0 46px;
    max-width: 1150px;
    margin: 0 auto;
  }
}
.modal__container {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9);
  top: 0;
  left: 0;
  display: none;
  z-index: 3;
}
.modal--active {
  display: flex;
}
.modal {
  position: relative;
  margin: auto;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  border: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
  padding: 29px 30px 46px;
  min-width: 320px;
  z-index: 3;
}
.modal__title {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.modal__title span {
  text-align: center;
  text-transform: uppercase;
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
}
.modal__title p {
  font-weight: 400;
  font-size: 15px;
  line-height: 30px;
  color: #444444;
  text-align: center;
  margin-top: 15px;
  margin-bottom: 22px;
}
.modal__button {
  width: 260px;
  height: 52px;
  background-color: #63D1BB;
  border: none;
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  color: #ffffff;
  margin: 31px auto 0;
  align-self: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .modal {
    min-width: 566px;
    height: 336px;
    padding-top: 37px;
  }
  .modal__title span {
    font-size: 30px;
    line-height: 57px;
  }
  .modal__title p {
    font-size: 17px;
    margin-top: 13px;
    margin-bottom: 34px;
  }
  .modal__button {
    margin-top: 45px;
  }
}
.block__title {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
  margin: 0;
}
@media (min-width: 768px) {
  .block__title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .block__title {
    font-size: 53px;
    line-height: 57px;
  }
}
.zigzag-fill {
  margin-left: -50px;
}
.zigzag-fill--desktop {
  display: none;
}
.zigzag-fill--tablet {
  display: none;
}
.zigzag-line {
  max-width: 320px;
  margin: 0 auto;
}
.zigzag-line--desktop,
.zigzag-line--tablet {
  display: none;
}
@media (min-width: 768px) {
  .zigzag-fill--tablet {
    display: block;
    max-width: 768px;
    margin: 0 auto 0;
  }
  .zigzag-fill__main {
    margin-left: -50px;
  }
  .zigzag-fill__main .zigzag-fill--tablet {
    margin-top: -24px;
  }
  .zigzag-line {
    max-width: 768px;
    margin: 0 auto;
  }
  .zigzag-line--desktop,
  .zigzag-line--mobile {
    display: none;
  }
  .zigzag-line--tablet {
    display: block;
    margin-left: -50px;
  }
}
@media (min-width: 1150px) {
  .zigzag-fill {
    margin-top: -21px;
    margin-left: -50px;
  }
  .zigzag-fill--tablet {
    display: none;
  }
  .zigzag-fill--desktop {
    display: block;
    max-width: 1150px;
  }
  .zigzag-fill__main .zigzag-fill--desktop {
    margin-top: -17px;
  }
  .zigzag-line {
    max-width: 1150px;
    margin: 0 auto;
  }
  .zigzag-line--tablet,
  .zigzag-line--mobile {
    display: none;
  }
  .zigzag-line--desktop {
    display: block;
  }
}
.user-list {
  margin: 0;
  padding: 0;
}
.user-list__item {
  list-style: none;
  padding: 27px 0 27px 76px;
  border-top: 1px solid #ececec;
}
.user-list__item:last-child {
  border-bottom: 1px solid #ececec;
}
.user-list__link-icon {
  opacity: 0.2;
  position: absolute;
  left: -45px;
  top: 2px;
  transition-timing-function: ease-out;
  transition-duration: 0.1s;
}
.user-list__link {
  text-decoration: none;
  position: relative;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
}
.user-list__link span {
  transition-timing-function: ease-out;
  transition-duration: 0.1s;
}
.user-list__link:hover .user-list__link-icon {
  fill: #63D1BB;
  opacity: 1;
}
.user-list__link:active .user-list__link-icon {
  opacity: 0.3;
}
.user-list__link:hover span {
  opacity: 0.6;
}
.user-list__link:active span {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .user-list {
    display: flex;
    align-self: flex-end;
  }
  .user-list__item {
    border-left: 1px solid #ececec;
    padding: 32px 32px 32px;
  }
  .user-list__item:last-child {
    border-bottom: none;
    padding-right: 50px;
  }
  .user-list__link-icon {
    position: unset;
  }
  .user-list__link-search {
    display: none;
  }
  .user-list__link span {
    margin-left: 25px;
  }
}
@media (min-width: 1150px) {
  .user-list {
    margin-right: 18px;
  }
