.modal form {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.modal-form {
  border: none;
}
.modal-form__input input {
  appearance: none;
  display: none;
}
.modal-form__list {
  max-width: 260px;
  margin: 0 auto;
}
.modal-form__button-radio::before {
  content: "";
  padding: 12px 33px;
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  text-transform: uppercase;
  background-color: transparent;
  border: 2px solid rgba(68, 68, 68, 0.1);
  transition-timing-function: ease-out;
  transition-duration: 0.1s;
}
.modal-form__button-radio--first::before {
  content: "s";
}
.modal-form__button-radio--second::before {
  content: "m";
}
.modal-form__button-radio--third::before {
  content: "l";
}
.modal-form__input:hover .modal-form__button-radio::before {
  border: 2px solid rgba(68, 68, 68, 0.3);
}
.modal-form__input:active .modal-form__button-radio::before {
  border: 2px solid #63D1BB;
}
.modal-form__input input[type="radio"]:checked ~ .modal-form__button-radio::before {
  border: 2px solid #63D1BB;
}
@media (min-width: 768px) {
  .modal-form__list {
    max-width: 350px;
  }
